.mainContainer{

    display: flex;
    align-items: center;


}

.bloco{

    display: flex;
    align-items: center;

}
.circleContainer{
    

    width: 400px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
    animation: inicial 1s;
    animation-fill-mode: forwards;
    
    
}
.circleContainer:hover{


    animation: hover 1s;
    animation-fill-mode: forwards;

}
@keyframes hover {

    from{

        clip-path: circle(40%);

    }
    to{

        clip-path: circle(37%);

    }
    
}


@keyframes inicial {

    from{

        clip-path: circle(37%);

    }
    to{

        clip-path: circle(40%);

    }
    
}

.img{

    height: 80%;
}
.img2{

    height: 120%;
}
.box{
   
    display: flex;
    flex-direction: column;
    padding: .8em 1em;
    width: 800px;
    min-height: 300px;
    height: auto;
    background-color: #efefef;
    -webkit-box-shadow: 8px 7px 21px -3px rgba(0,0,0,0.75);
    -moz-box-shadow: 8px 7px 21px -3px rgba(0,0,0,0.75);
    box-shadow: 8px 7px 21px -3px rgba(0,0,0,0.75);
    transition: 1s;
    border-radius: 20px;


}
.box h2{

    font-size: 20px;
    margin-bottom: 1em;
  
}
.box p{

    margin-left: .5em;


}
.title{


    margin-bottom: .2em;

}
.texto{

    margin-top: .5em;
    font-size: 18px;


}
@media only screen and (max-width: 900px) {


    .circleContainer{

        width: 0;
        height: 0;
    }
    .box{

        margin-bottom: 3em;
        height: auto;
        margin-left: 1.4em;
        margin-right: 1.4em;
 
    }
    .title{

        font-size: 22px;
        text-align: center;
        

    }
    .box h2{

        font-size: 17px;
        text-align: center;

    }


}
@media only screen and (max-width: 1200px) {

    .circleContainer{

        width: 0;
        height: 0;
    }
    .box{

        margin-bottom: 3em;
        min-width: 90vw;
        height: auto;
        width: auto;
        
 
    }

}