.homeContainer{
    margin: 4em;
    display: flex;
    flex-direction: column;
    width: 100%;   
}
.tituloPage{

    text-align: center;
    padding: 3em;
    padding-top: 1em;
    font-size: 37px;

}
.tituloPage span{
    color: #efefef;
    animation: back 1s .4s;
    padding-left: .2em;
    padding-right: .2em;
    transition: 2s;
    animation-fill-mode: forwards;
}
.tituloPage span1{

    text-decoration: underline;

}
@keyframes back {
    
    from{
        background-color: none;


    }
    to{
        background-color: #222;

    }

}
@media only screen and (max-width: 1200px){

    .homeContainer {

        align-items: center;


    }


}
@media only screen and (max-width: 640px){

    .tituloPage{

       padding-left: .1em;
       padding-right: .1em; 
       font-size: 25px;

    }


}