.container{

    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2em;

}
.navbar{
    display: flex;
    justify-content: space-between;
    background-color: #222;
    padding: .8em ;
    padding-left: 4em;
    padding-right: 0;
    
}
.link{

    display: flex;
    text-decoration: none;
    align-items: center;
    width: 50%;

}
.logo{

    font-size: 80px;
    
}
.title{

    font-size: 17px;
    color: #efefef;
    text-align: center;
    margin-left: 2em;
    transition: .2s;
    font-weight: normal;
}
.title:hover{

    color: rgb(214, 211, 22);

}
#navbar{
    
    position: absolute;
    right: 2.5em;
    display: flex;
    list-style: none;
    align-items: center;
    background-color: transparent;

}
.item{

    text-decoration: none;
    color: #efefef;
    padding: 1em;
    transition: .2s;
    font-size: 18px;
}

.item:hover{

    color: rgb(214, 211, 22);

}
.container div{

    display: none;

}
.container div i{

    color: #fff;

}


@media only screen and (max-width: 1102px) {

    .navbar{

        position: sticky;
        top: 0;
        z-index: 85;
        padding-left: 1em;

    }
    .link{

        width: auto;

    }
    .logo{
        padding: 0;

    }
    #navbar{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        position: fixed;
        top: 90px;
        right: -300px;
        width: 300px;
        height: 100vh;
        box-shadow: 0 40px 60px rgba(0, 0, 0, 0);
        padding: 40px 0 0 30px; 
        transition: .6s ease-in-out;
    }
    #navbar.active{

        right: 0px;
        background-color: #222;

    }
    #navbar li{

        margin-bottom: 25px;

    }
    .item{

        font-size: 20px;
        font-weight: bold;
        padding: 0;


    }
    .container div{

        display: block;
    
    }
    .container div i{

        font-size: 24px;
        cursor: pointer;
    
    }
}
@media only screen and (max-width: 500px) {


    .link{

        width: 280px;


    }


}