
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 200px;
    margin-top: 3em;    
}
.teste_titulo{

    text-align: center;
    padding-left: 7em;
    padding-right: 7em;

}
.teste_titulo span{
    color: #efefef;
    animation: back 1s;
    padding-left: .2em;
    padding-right: .2em;
    transition: 2s;
    animation-fill-mode: forwards;
}
.painel{
    margin-top: 6em;
    margin-bottom: 6em;
    position: relative;
    height: 570px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    font-size: 15px;
    text-align: center;
    width: 50vw;
    background-color: #222;
    border-radius: 15px;
    color: white;
    font-family: coolvetica;
    letter-spacing: 4px;
    font-size: 12px;
    padding: 3em;
}
.btn{

    position: relative;
    margin-top: 8px;
    background-color: #222;
    border: 1px solid rgb(255, 255, 255);
    height: 50px;
    width: 250px;
    padding: 15px;
    color: white;
    border-radius: 15px;
    transition: 0.3s;
    visibility: visible;
  
}
.btn:hover{
    cursor: pointer;
    transition: 0.3s;
    background-color: rgba(251, 255, 0, 0.527);


}
@media only screen and (max-width: 900px) {
    
    .teste_titulo{
        
        font-size: 25px;
        text-align: center;
        
    }
    .painel{
            
        width: 80vw;
        
    }

  
 }
 @media only screen and (max-width: 470px) {
    
    .teste_titulo{
        
        padding-left: 0;
        padding-right: 0;
        text-align: center;
        
    }
    .painel{

        padding-left: 2em;
        padding-right: 2em;
        font-size: 10px;
        width: 90vw;
        height: auto;
        min-height: 570px;
        
    }

  
 }
.resultado{

    position: relative;
    font-size: 25px;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 15px;
    padding: 15px;


}

.final{

    position: relative;
    font-size: 25px;
    letter-spacing: 2px;
    text-align: center;
    border-radius: 5px;
    background-color: #222;
    border: 1px solid rgb(255, 255, 255);
    padding: 15px 15px 15px;
    transition: 0.3s;



}
.pergunta{

    transition: 0.3s;
    text-shadow: 2px 2px black;
    margin-bottom: 1em;

}

.return{

    position: relative;
    margin-top: 8px;
    background-color: #222;
    border: 1px solid rgb(255, 255, 255);
    height: 50px;
    width: 250px;
    padding: 15px;
    color: white;
    border-radius: 15px;
    transition: 0.3s;


    
}
.return:hover{
    cursor: pointer;
    transition: 0.3s;
    background-color: rgba(251, 255, 0, 0.527);

}
@keyframes back {
    
    from{
        background-color: none;


    }
    to{
        background-color: #222;

    }

}