.container{

    display: flex;
    flex-direction: column;
    padding: 3em;
    padding-bottom: 9em;

}
.title{

    margin-bottom: 2.5em;

    color: white;
    text-align: center;
    animation: back 1s .4s;
    animation-fill-mode: forwards;
}
.container p {

    font-size: 22px;
    margin-top: .8em;
    margin-left: 1em;
    margin-right: 1em;
    text-align: center;

}
@keyframes back {
    
    from{
        background-color: none;


    }
    to{
        background-color: #222;

    }

}
@media only screen and (max-width: 640px){

    .container p{

        font-size: 18px;
        margin-left: 0em;
        margin-right: 0em;
        text-align: center;
        margin-bottom: 1em;


    }


}