.container{
    display: flex;
    flex-direction: column;
    padding: 2em;
    max-width: 400px;
    align-items: center;

}
.container:hover{

    color: rgb(214, 211, 22);
    transition: .3s;
    cursor: pointer;

}
.container a{

    text-decoration: none;
    color: black;
    
    
}
.container a:hover{

    text-decoration: underline;
    
}

.imagem{

    width: auto;
    max-width: 350px;
    max-height: 12em;
    min-height: 12em;
    border-radius: 3px;
    -webkit-box-shadow: 0px 4px 7px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 4px 7px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 4px 7px 0px rgba(0,0,0,0.75);
    transition: .2s;
}
.imagem:hover{

    cursor: pointer;
    border-radius: 8px;

}
.titulo{
    max-width: 280px;
    margin-top: .2em;
    font-size: 20px;
}
.texto{

    font-size: 12px;
    color: gray;
    font-weight: 50;

}
