.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 3em;
    padding-bottom: 3em;
}
.tituloPage{

    text-align: center;
    padding: 3em;
    padding-top: 3em;
    padding-bottom: 1em;

}
.tituloPage span{
    color: #efefef;
    animation: back 1s;
    padding-left: .2em;
    padding-right: .2em;
    transition: 2s;
    animation-fill-mode: forwards;
}

@keyframes back {
    
    from{
        background-color: none;


    }
    to{
        background-color: #222;

    }

}
@media only screen and (max-width: 680px) {

    .tituloPage{

        padding-left: .5em;
        padding-right: .5em;
        font-size: 25px;

    }

}